<template>
  <d2-container>
    <div style="width: 95%;height: 95%;" class="chart">
      <div id="myChart" :style="{width: '95%', height: '95%'}"></div>
    </div>
  </d2-container>
</template>
<script>
  import { statistics } from '../../plugin/axios/api.js';
  import { Myerror } from '../../plugin/axios/er.js';
  // 
  let echarts = require('echarts/lib/echarts')
  // 
  require('echarts/lib/chart/bar')
  // title
  require('echarts/lib/component/tooltip')
  require('echarts/lib/component/title')
  export default {
    data() {
      return {

      }
    },
    mounted() {
      this.drawLine();
    },
    methods: {
      drawLine() {
        statistics().then(res => {
          console.log(res);
          let key = [], val = [];
          if (res.code == 200) {
            for (const keys in res.data) {
              key.push(keys)
              val.push(res.data[keys])
            }
            // console.log(key, val);
            this.ehcratmy(key, val)
          }
          Myerror(res, this)
        })

      },
      ehcratmy(key, val) {
        // domecharts
        let myChart = echarts.init(document.getElementById('myChart'))
        // 
        myChart.setOption({
          title: { text: '' },
          tooltip: {},
          xAxis: {
            data: key
          },
          yAxis: {},
          series: [{
            name: '',
            type: 'bar',
            data: val,
            itemStyle: {
              //
              normal: {
                //colorListcolorList
                color: function (params) {
                  var colorList = ['rgb(164,205,238)', 'rgb(42,170,227)', 'rgb(25,46,94)', 'rgb(195,229,235)'];
                  return colorList[params.dataIndex];
                }
              },
              //
              emphasis: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            },
          }]
        });
      }
    }
  }
</script>
